import React, { useState } from "react";
import "./App.css";
import NavBar from "./Navbar";
import Home from "./Home";
import Cart from "./Cart";
import Products from "./Products";
import Profile from "./MyProfile";
import ProductDetails from "./ProductDetails";
import {
  BrowserRouter as Router,
  Route,
  Routes,
  Switch
} from "react-router-dom";

const App = () => {
  const [searchedValue, updateSearched] = useState("");
  const searchedItem = (value) => {
    updateSearched(value);
  };
  const [cartData, updatecartData] = useState([]);
  const updateCart = (value) => {
    updatecartData((prevState) => [...prevState, value]);
  };
  return (
    <Router>
      <div className="App">
        <NavBar searched={searchedItem} cartData={cartData} />
        <Routes>
          <Route path="" exact element={<Home searched={searchedValue} />} />
          <Route
            path="/Home"
            exact
            element={<Home searched={searchedValue} />}
          />
          <Route path="/Cart" exact element={<Cart />} />
          <Route
            path="/Categories/"
            exact
            element={
              <div className="productsContainer">
                <Products searched={searchedValue} />
              </div>
            }
          />
          <Route
            path="/ProductDetails"
            exact
            element={<ProductDetails cartAdded={updateCart} />}
          />
          <Route path="/Profile" exact element={<Profile />} />
        </Routes>
      </div>
    </Router>
  );
};

export default App;
