import React from "react";
import "./App";
import { Link } from "react-router-dom";
import { FaShoppingCart } from "react-icons/fa";

const NavBar = (props) => {
  const navstyle = {
    color: "white"
  };
  const cartData = props.cartData;
  const searchChanged = (e) => {
    props.searched(e.target.value);
  };
  return (
    <nav className="Nav-Bar">
      <ul className="Nav-links">
        <Link to="/Home" style={navstyle}>
          <li>Home</li>
        </Link>
        <Link to="/Categories" style={navstyle}>
          <li>Products</li>
        </Link>
        <Link to="/Profile" style={navstyle}>
          <li>Profile</li>
        </Link>
        <Link to="/Cart" style={navstyle}>
          <li>Cart</li>
        </Link>
      </ul>
      <div>
        <input
          className="searchBox"
          onChange={searchChanged}
          placeholder="search"
        />
      </div>
      <div className="Cart">
        <span className="cartIcon">
          <FaShoppingCart />
        </span>
        <span className="cartdata">{cartData.length}</span>
      </div>
    </nav>
  );
};

export default NavBar;
