import "./App";
import axios from "axios";
import { Link } from "react-router-dom";
import React, { useState, useEffect } from "react";

const Products = (props) => {
  let query = window.location.search.substring(1);
  !query && (query = "electronics");

  const [ProductSelected, SetProductSelected] = useState(null);
  useEffect(() => {
    axios
      .get("https://fakestoreapi.com/products/category/" + query)
      .then((req) => {
        SetProductSelected((prevData) => req.data);
        console.log(req.data);
      })
      .catch((e) => {
        console.log(e);
      });
  }, []);

  return ProductSelected
    ? ProductSelected.filter((item) =>
        item.title.toLowerCase().includes(props.searched.toLowerCase())
      ).map((data, index) => (
        <Link
          key={index}
          className="productContainer"
          to={"/ProductDetails?" + data.id}
        >
          <div>
            <div className="productImage">
              <img src={data.image} alt="" />
            </div>
            <div className="Producttitle">
              <h2>{data.title}</h2>
            </div>
            <div className="Category">{data.category}</div>
            <div>${data.price}</div>
          </div>
        </Link>
      ))
    : "loading please wait...";
};

export default Products;
