import "./App";
import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { FaStar } from "react-icons/fa";

const ProductDetails = (props) => {
  const query = window.location.search.substring(1);
  const [SelectedProduct, setSelectedProduct] = useState(null);
  //fetching the data from API
  useEffect(() => {
    axios
      .get("https://fakestoreapi.com/products/" + query)
      .then((req) => {
        setSelectedProduct(req.data);
      })
      .catch((e) => {
        console.log(e);
      });
  }, []);
  const addtoCart = () => {
    SelectedProduct && props.cartAdded(SelectedProduct.id);
  };
  return (
    <div className="API3">
      <div>
        {SelectedProduct ? (
          <div className="ProductDetails">
            <div id="Productimage">
              <img src={SelectedProduct.image} alt="" />
            </div>
            <div className="productRight">
              <div id="Producttitle">{SelectedProduct.title}</div>
              <div className="ProductDescp">{SelectedProduct.description}</div>
              {/* <div className="Productrating">{[SelectedProduct.rating]}</div> */}
              <div className="Productprice">${SelectedProduct.price}</div>
              <div>
                <span className="ratings">{SelectedProduct.rating.rate}/5</span>
                <span className="ratingsCount">
                  {SelectedProduct.rating.count}+Ratings
                </span>
              </div>
              <div>
                <button className="buyBtn" onClick={addtoCart}>
                  add to cart
                </button>
                <button className="buyBtn">buy now</button>
              </div>
            </div>
          </div>
        ) : (
          "loading..."
        )}
      </div>
    </div>
  );
};

export default ProductDetails;
